import { Component, OnInit, TemplateRef } from '@angular/core';
import { select, Store } from '@ngrx/store';
import { filter } from 'rxjs/operators';
import { AppState, getCompany } from '../store/reducers';
import { UserService } from '../api/user.service';
import { CompanyService } from '../api/company.service';
import { TableHeaderItem } from '../shared/table-base/table-header-item.interface';
import { MemberModalComponent } from '../shared/manage-user-modal/member-modal.component';
import { BsModalRef, BsModalService } from 'ngx-bootstrap';
import { User } from '../types/user';

import { BaseComponent } from '../shared/helpers/base.component';
import { Company, StoreCompany } from '../types/company';

@Component({
  selector: 'upb-members',
  templateUrl: './members.component.html',
  styleUrls: ['./members.component.scss']
})
export class MembersComponent extends BaseComponent implements OnInit {
  public toast: any;
  public showNotification: boolean;
  public users: User[];
  public searchString: string;
  public bsModalRef: BsModalRef;
  public loading: boolean = false;
  public company: StoreCompany;
  public selectedCompany: Company;
  public selectedUser: User;
  public userToDelete: User;
  public membersCanOnlyListenOwnCalls: boolean = false;
  public header: TableHeaderItem[] = [
    {
      key: 'first_name',
      title: 'First name'
    },
    {
      key: 'last_name',
      title: 'Last name'
    },
    {
      key: 'email',
      title: 'Email'
    },
    {
      key: 'role',
      title: 'Role'
    },
    {
      key: 'last_seen',
      title: ''
    },
    {
      key: 'actions',
      title: 'Actions'
    }
  ];

  constructor(
    private store: Store<AppState>,
    private userService: UserService,
    private companyService: CompanyService,
    private modal: BsModalService
  ) {
    super();
  }

  ngOnInit(): void {
    this.subs = this.store
      .pipe(
        select(getCompany),
        filter(Boolean)
      )
      .subscribe((company: StoreCompany) => {
        this.company = company;
        this.selectedCompany = company.selectedCompany;
        if (
          this.selectedCompany.features &&
          this.selectedCompany.features.membersCanOnlyListenOwnCalls &&
          this.selectedCompany.features.membersCanOnlyListenOwnCalls.active
        ) {
          this.membersCanOnlyListenOwnCalls = this.selectedCompany.features.membersCanOnlyListenOwnCalls.active;
        }
        this.getMembers();
      });
  }

  public createUser(): void {
    this.bsModalRef = this.modal.show(MemberModalComponent, {
      class: 'modal-lg',
      backdrop: 'static'
    });
    this.bsModalRef.content.company = this.selectedCompany;
    this.bsModalRef.content.users = this.users;
    this.subs = this.bsModalRef.content.event.subscribe(() => this.getMembers());
  }

  public editUser(user: User): void {
    this.selectedUser = user;
    this.bsModalRef = this.modal.show(MemberModalComponent, {
      class: 'modal-lg',
      backdrop: 'static'
    });
    this.bsModalRef.content.user = user;
    this.bsModalRef.content.users = this.users;
    this.bsModalRef.content.company = this.selectedCompany;
    this.subs = this.bsModalRef.content.event.subscribe(() => this.getMembers());
  }

  public sendEmail(user: User): void {
    this.subs = this.userService.sendEmail(user.id).subscribe(
      () => {
        this.showNotification = true;
        this.toast = {
          message: 'Email was sent.',
          type: 'success'
        };
      },
      () => {
        this.showNotification = true;
        this.toast = {
          message: 'Cannot send email. Please try again.',
          type: 'danger'
        };
      }
    );
  }

  private getMembers(): void {
    this.subs = this.companyService
      .getCompanyUsers(this.company.selectedCompany.id)
      .subscribe(users => (this.users = users));
  }

  public updateMembersCanOnlyListenOwnCalls(): void {
    const features: any = this.selectedCompany.features;
    features.membersCanOnlyListenOwnCalls = {
      name: 'Members Can Only Listen to Their Own Calls',
      id: 'membersCanOnlyListenOwnCalls',
      active: !this.membersCanOnlyListenOwnCalls
    };
    this.companyService.updateFeatures(this.selectedCompany.id, { features }).subscribe(
      () => {
        this.selectedCompany.features = features;
        this.showNotification = true;
        this.toast = {
          message: 'Feature updated.',
          type: 'success'
        };
      },
      () => {
        this.showNotification = true;
        this.toast = {
          message: 'Unable to update feature. Please try again.',
          type: 'danger'
        };
      }
    );
  }

  public confirmDelete(): void {
    this.subs = this.companyService.deleteCompanyUser(this.company.selectedCompany.id, this.userToDelete.id).subscribe(
      () => {
        this.users = this.users.filter(c => c.id !== this.userToDelete.id);
        this.showNotification = true;
        this.toast = {
          message: 'User deleted.',
          type: 'success'
        };
      },
      () => {
        this.showNotification = true;
        this.toast = {
          message: 'Cannot delete user.',
          type: 'danger'
        };
      }
    );
    this.bsModalRef.hide();
  }

  public decline(): void {
    this.bsModalRef.hide();
  }

  public deleteUser(template: TemplateRef<any>, user: User): void {
    this.userToDelete = user;
    this.bsModalRef = this.modal.show(template, { class: 'modal-sm' });
  }
}
